import { MusicExternalApiResourceLanguage } from '@yandex-music-int/resource-collection';
import { DeepPartial } from '@yandex-music-int/ts-utility-types';

import { TLD_MARK } from '../router/constants/tldMark.js';

import { ConfigVariables } from './types/ConfigVariables.js';

const getRumPlatformSettings = (): Pick<
    ConfigVariables['rumSettings'],
    'rumId' | 'project' | 'service' | 'platform'
> => {
    if (process.env.BUILD_TYPE === 'desktop') {
        return {
            rumId: 'ru.music.frontend',
            project: 'music.frontend.desktop',
            service: 'frontend-desktop',
            platform: 'desktop'
        };
    }

    return {
        rumId: 'ru.music.frontend',
        project: 'music.frontend.web',
        service: 'frontend-web',
        platform: 'desktop'
    };
};

const getErrorBoosterSettings = (): Pick<ConfigVariables['rumSettings'], 'project' | 'platform'> => {
    if (process.env.BUILD_TYPE === 'desktop') {
        return {
            project: 'music.frontend.desktop',
            platform: 'desktop'
        };
    }

    return {
        project: 'music.frontend.web',
        platform: 'desktop'
    };
};

// Отключаем правила, так как невозможно сделать определение возвращаемого типа корректно
// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types, @typescript-eslint/explicit-function-return-type
export const config = () =>
    ({
        client: 'YandexMusicWeb/1.0',
        language: MusicExternalApiResourceLanguage.RU,
        oauth: process.env.NEXT_PUBLIC_MUSIC_FRONTEND_OAUTH ?? '',
        dev: {
            panel: false
        },
        logger: {
            // eslint-disable-next-line no-constant-binary-expression
            uaDisabled: Boolean(process.env.SERVER_UNIFIED_AGENT_DISABLED) ?? false,
            uaServerAddress: process.env.SERVER_UNIFIED_AGENT_ADDRESS ?? 'http://localhost:12344',
            errorBooster: {
                ...getErrorBoosterSettings(),
                page: 'window.location.pathname',
                version: process.env.APP_VERSION || '',
                environment: process.env.NODE_ENV,
                unhandledRejection: true,
                uncaughtException: true,
                resourceFails: true
            }
        },
        oauthCredentials: {
            host: `https://oauth.yandex.${TLD_MARK}`,
            clientId: '97fe03033fa34407ac9bcf91d5afed5b'
        },
        tvm: {
            tvmtoolAuthToken: process.env.TVMTOOL_LOCAL_AUTHTOKEN,
            self: 'music_test',
            destinations: ['blackbox'],
            host: 'http://localhost:32272',
            enabled: false
        },
        rumSettings: {
            ...getRumPlatformSettings(),
            page: 'window.location.pathname',
            heroElement: 'body',
            version: process.env.APP_VERSION || '',
            environment: process.env.NODE_ENV
        },
        passportCredentials: {
            host: `https://passport.yandex.${TLD_MARK}`,
            origin: 'music'
        },
        blackbox: {
            host: 'http://blackbox.yandex.net/blackbox',
            enabled: false
        }
    } satisfies DeepPartial<ConfigVariables>);
