import { Platform } from '../types/Platform.js';

import { getPlatform } from './getPlatform.js';

export function getPlatformSecret(): string {
    let secret = '';

    switch (process.env.BUILD_TYPE) {
        case 'web':
            secret = process.env.NEXT_PUBLIC_YANDEX_MUSIC_WEB_SECRET;
            break;
        case 'desktop': {
            const platform = getPlatform();
            switch (platform) {
                case Platform.MACOS:
                    secret = process.env.NEXT_PUBLIC_YANDEX_MUSIC_DESKTOP_MACOS_SECRET;
                    break;
                case Platform.WINDOWS:
                    secret = process.env.NEXT_PUBLIC_YANDEX_MUSIC_DESKTOP_WINDOWS_SECRET;
                    break;
            }
            break;
        }
    }

    return secret;
}
